import React from 'react';
import ReactDOM from 'react-dom';

import {PagesContainer, MainContainer, Faces} from './commComponent.js';
import {golaburl} from "./comm1.js";

import '../css/index.css';

class Page2 extends React.Component {
  render() {
    return <PagesContainer dataClass="wrapper">
      <Main/>
    </PagesContainer>
  }
}

class Main extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return <MainContainer>
      <div className="arrival">
        <FormBox/>
        <Table/>
      </div>
    </MainContainer>
  }
}

class FormBox extends React.Component {
  constructor(prop) {
    super(prop);
  }

  render() {
    return <div className="arrival-form box">
      <div>
        <h6> 13700000000 GS8 </h6>
        <p></p>
        <textarea name="" id=""/>
        <p>
          <span></span>
          <label className="radio"><input type="radio" name="drive" id=""/><span/><span></span></label>
          <label className="radio"><input type="radio" name="drive" id=""/><span/><span></span></label>
        </p>
      </div>
      <div>
        <button className="btn btn-primary"></button>
      </div>
    </div>;
  }
}

class Table extends React.Component {
  constructor(prop) {
    super(prop);
  }

  render() {
    return <div className="table box">
      <table>
        <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        <Tr/>
        </tbody>
      </table>
    </div>;
  }
}

class Tr extends React.Component {
  constructor(prop) {
    super(prop);

    let _this = this;
    fetch(golaburl() + 'src/data/page2.json', {credentials: 'include'})
      .then((response) => response.json())
      .then((data) => _this.setState(data));

    this.state = {
      datas: [],
    };

  }

  render() {
    const b = this.state.datas.map((v, i) =>
      <tr key={'a' + i.toString()}>
        <td>
          <Faces url={v.url}/>
        </td>
        <td>
          <p>{v.in}</p>
          <p></p>
          <p>{v.out}</p>
        </td>
        <td>{v.state}</td>
      </tr>
    );

    return b;
  }
}

export {Page2};
