import React from "react";
import {lazyImg} from "./comm1";

class PagesContainer extends React.Component {
  constructor(props){
    super(props);
    this.props = props;
  }
  render() {
    return <div className={this.props.dataClass}>
      {this.props.children}
    </div>
  }
}

class MainContainer extends React.Component {
  constructor(props){
    super(props);
    this.props = props;
  }
  render() {
    return <main className={this.props.dataClass}>
      {this.props.children}
    </main>
  }
}

class Faces extends React.Component{
  constructor(prop){
    super(prop);
    this.state = {
      url: prop.url
    };
    this.face = React.createRef();
  }

  // 
  componentDidMount() {
    if (!!this.face.current) {
      lazyImg([this.face.current]);
    }
  }

  // 
  componentDidUpdate() {
    if (!!this.face.current) {
      lazyImg([this.face.current]);
    }
  }

  render(){
    return <div ref={this.face} className="face" data-src={this.state.url}/>
  }
}

export {
  PagesContainer,
  MainContainer,
  Faces
}
