import React from 'react';
import ReactDOM from 'react-dom';
import {TransitionGroup, CSSTransition} from "react-transition-group";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter,
  Switch
} from "react-router-dom";
import {renderRoutes} from 'react-router-config'

import {golaburl} from "./comm1.js";
import {Page1} from './page1.js';
import {Page2} from './page2.js';

let url = (function () {
  let g = golaburl();
  return g.length ? '/cjatech/dist/' : '/';
}());


const routes = [
  {
    path: url,
    exact: true,
    component: Page1
  },
  {
    path: url + 'page2',
    component: Page2,
  },
  {
    path: url + '*',
    component: () => <div className="wrapper">404</div>
  }
];

const BasicExample = () => (
  <Router>
    <Route
      render={({location}) => (
        <div>
          <div className="nav">
            <Link to={url + ''}>Page1</Link>
            <Link to={url + 'page2'}>Page2</Link>
            <Link to={url + 'page3'}>Page3</Link>
            <Link to={url + 'page4'}>Page4</Link>
          </div>
          <TransitionGroup>
            <CSSTransition key={location.key} classNames="fade" timeout={500}>
              <Switch location={location}>
                {/*react-router-config*/}
                {renderRoutes(routes)}

                {/*router-config*/}
                {/*{routes.map((route, i) => <RouteWithSubRoutes key={i} {...route} />)}*/}

                {/*react-router-config*/}
                {/*<Route exact path="/cjatech/dist/" component={Page1}/>
                <Route path="/cjatech/dist/page2" component={Page2}/>*/}


                <Route render={() => <div>404</div>}/>
              </Switch>
            </CSSTransition>
          </TransitionGroup>
        </div>
      )}
    />
  </Router>
);


const RouteWithSubRoutes = route => (
  <Route
    path={route.path}
    render={props => (
      // pass the sub-routes down to keep nesting
      <route.component {...props} />
    )}
  />
);

ReactDOM.render(
  <BasicExample/>,
  document.querySelector('#root')
);
