import {createStore} from 'redux';
import {golaburl} from "./comm1.js";

const getAjaxData = function (url) {
  return new Promise(function (resolve, reject) {
    fetch(url, {credentials: 'include'})
      .then((response) => response.json())
      .then((data) => resolve(data));
  });
};

const getHeaderData = function () {
  return getAjaxData(golaburl() + 'src/data/header.json');
};

const getListData = function () {
  return getAjaxData(golaburl() + 'src/data/lists.json');
};

const gen = function* (cb) {
  return cb();
};

function counter(state = [], action) {
  switch (action.type) {
    case 'getHeaderData':
      action.getHeaderData = gen(getHeaderData).next();
      return action;
    case 'getListData':
      action.getListData = gen(getListData).next();
      return action;
    case 'laym':
      return action;
    case 'filter':
      return action;
    default:
      return state;
  }
}

let store = createStore(counter);

function getStateData(cb) {
  return store.subscribe(() => {
    let stateData = store.getState();
    switch (stateData.type) {
      case 'getHeaderData':
      case 'getListData':
        stateData[stateData.type].value.then(function (data) {
          cb(data);
        });
        break;
      case 'laym':
        // console.log('laym1');
        cb(stateData);
        break;
      case 'filter':
        // console.log('filter1');
        cb(stateData);
        break;
    }
  });
}

export {
  store,
  getStateData
}
