function setDefFontSize() {
  // 
  let vw = window.innerWidth / 100;
  let vh = window.innerHeight / 100;
  let vmax = Math.max(vw, vh);
  document.querySelector('html').style.fontSize = 3 * vmax + 'px';
}

function setMobileFontSize() {
  if (isMobile().any()) {
    setDefFontSize();
    window.addEventListener('resize', function() {
      setDefFontSize();
    });
  } else {
    document.querySelector('html').style.fontSize = 14 + 'px';
  }
}

function isMobile() {
  let isMobile = {
    Android: function() {
      return navigator.userAgent.match(/Android/i) ? true : false;
    },
    BlackBerry: function() {
      return navigator.userAgent.match(/BlackBerry/i) ? true : false;
    },
    iOS: function() {
      return navigator.userAgent.match(/iPhone|iPad|iPod/i) ? true : false;
    },
    Windows: function() {
      return navigator.userAgent.match(/IEMobile/i) ? true : false;
    },
    any: function() {
      return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Windows());
    }
  };
  return isMobile;
}

let lazyImg = function(imgs) {
  imgs.map(function(o, i) {
    o.className += ' loading';
    let src = o.getAttribute('data-src');
    let img = new Image;
    img.src = src;
    img.onload = function() {
      o.className = o.className.replace(/\bloading\b/g, '');
      o.style.backgroundImage = 'url("' + src + '")';
    };
  });
};

function randomRange(start, end) {
  let total = end - start + 1;
  return Math.floor(Math.random() * total + start);
}

Date.prototype.format = function(fmt) {
  let o = {
    "M+": this.getMonth() + 1, //
    "d+": this.getDate(), //
    "h+": this.getHours(), //
    "m+": this.getMinutes(), //
    "s+": this.getSeconds(), //
    "q+": Math.floor((this.getMonth() + 3) / 3), //
    "S": this.getMilliseconds() //
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  }
  for (let k in o) {
    if (new RegExp("(" + k + ")").test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    }
  }
  return fmt;
};

