import React from "react";
import {golaburl} from "./comm1.js";
import {store, getStateData} from "./reduxs.js";

class Header extends React.Component {
  constructor(props) {
    super(props);

    let _this = this;
    // $.getJSON(golaburl() + 'src/data/header.json')
    //   .done(function (data) {
    //     _this.setState(data);
    //   });
    // fetch(golaburl() + 'src/data/header.json', {credentials: 'include'})
    //   .then((response) => response.json())
    //   .then((data) => _this.setState(data));

    let unsubscribe = getStateData(function (data) {
      _this.setState(data);
    });

    store.dispatch({type: 'getHeaderData'});

    this.state = {
      sale: 0,
      complete: 0
    };

    this.logout = this.logout.bind(this);

    unsubscribe();
  }

  logout() {
    console.log('logout');
  }


  render() {
    return <header>
      <div className="info box">
        <div>
          <span>:</span>
          <span>{this.state.sale}</span>
        </div>
        <div>
          <span></span>
          <span>{this.state.complete}</span>
        </div>
        <div>
          <button className="btn btn-danger" onClick={this.logout}>
            <i className="fa fa-sign-out" aria-hidden="true"/><span></span>
          </button>
        </div>
      </div>
    </header>
  }
}

export {Header};
