import React from "react";
import ReactDOM from "react-dom";
import emitter from "./ev.js";

import {lazyImg, golaburl} from "./comm1.js";
import {Faces} from './commComponent.js';
import {getStateData, store} from "./reduxs";

class Btnbox extends React.Component {
  constructor(prop) {
    super(prop);
    this.check = this.check.bind(this);
    this.exclamation = this.exclamation.bind(this);
    this.retweet = this.retweet.bind(this);
    this.users = this.users.bind(this);
  }

  check() {
    store.dispatch({type: 'filter', filter: 1});
    // emitter.emit('filter', 1);
  }

  exclamation() {
    store.dispatch({type: 'filter', filter: 2});
    // emitter.emit('filter', 2);
  }

  retweet() {
    store.dispatch({type: 'filter', filter: 3});
    // emitter.emit('filter', 3);
  }

  users() {
    store.dispatch({type: 'filter', filter: 4});
    // emitter.emit('filter', 4);
  }

  render() {
    return <div className="btns box">
      <div className="btn btn-small btn-primary" onClick={this.check}>
        <i className="fa fa-check-circle" aria-hidden="true"/><span><br/></span>
      </div>
      <div className="btn btn-small btn-danger" onClick={this.exclamation}>
        <i className="fa fa-exclamation-circle" aria-hidden="true"/><span><br/></span></div>
      <div className="btn btn-small btn-info" onClick={this.retweet}>
        <i className="fa fa-retweet" aria-hidden="true"/><span><br/></span>
      </div>
      <div className="btn btn-small btn-success" onClick={this.users}>
        <i className="fa fa-users" aria-hidden="true"/><span><br/></span></div>
    </div>
  }
}

class Facelist extends React.Component {
  constructor(prop) {
    super(prop);

    let _this = this;
    // $.getJSON(golaburl + 'src/data/lists.json')
    //   .done(function (data) {
    //     _this.setState(data);
    //   });
    // fetch(golaburl() + 'src/data/lists.json', {credentials: 'include'})
    //   .then((response) => response.json())
    //   .then((data) => _this.setState(data));

    this.state = {
      datas: [],
    };

    let unsubscribe = getStateData(function (data) {
      _this.setState(data);
    });

    store.dispatch({type: 'getListData'});

    unsubscribe();

  }

  componentDidMount() {
    let _this = this;
    setTimeout(function () {

        let mySwiper = new Swiper('.swiper-container', {
          direction: 'vertical',
          scrollbar: {
            el: '.swiper-scrollbar',
          },
          slidesPerView: 'auto',
          mousewheel: true,
          freeMode: true,
          on: {
            touchMove: function (event) {
              // console.log(mySwiper.translate);
              if (mySwiper.translate < 50 && mySwiper.translate > 0) {
                $('.refresh').show();
              } else if (mySwiper.translate > 50) {
                $('.addrefesh').show();
              }
            },
            touchEnd: function (event) {
              let _viewHeight = document.querySelector('.face-list .swiper-wrapper').offsetHeight;
              let _scrollHeight = document.querySelector('.face-list .swiper-wrapper').scrollHeight;

              let datas = _this.state.datas;


              if (mySwiper.translate >= 50) {
                console.log("");
                let loading = layer.open({
                  content: '',
                  skin: 'msg'
                });
                setTimeout(function () {
                  console.log("update");
                  datas[1].state = 3;
                  _this.setState({
                    datas: datas
                  });
                  mySwiper.update();
                  layer.close(loading);
                }, 500);
              }

              if (mySwiper.translate <= _viewHeight - _scrollHeight && mySwiper.translate < 0) {
                console.log("");
                let loading = layer.open({
                  content: '',
                  skin: 'msg'
                });
                setTimeout(function () {
                  console.log("add");
                  datas.push({
                    "mainurl": "https://s.gravatar.com/avatar/eea4451a80bf24d23ead05eebaecbc3f?s=80",
                    "state": 22,
                    "enter": "2018-04-25 13:13:16",
                    "leave": "2018-04-25 13:13:16"
                  });
                  _this.setState({
                    datas: datas
                  });

                  mySwiper.update();
                  layer.close(loading);
                }, 500);
              }
            }
          }
        });


      }
    )
    ;
  }


  render() {

    const b = this.state.datas.map((v, i) =>
      <Box key={'a' + i.toString()} datas={v} index={i}/>
    );

    return <div className="face-list swiper-container">
      <div className="refresh"></div>
      <ul className="swiper-wrapper">{b}</ul>
      <div className="swiper-scrollbar"/>
      <div className="addrefesh"></div>
    </div>
  }
}

class Box extends React.Component {
  constructor(prop) {
    super(prop);
    this.state = prop.datas;
    this.state.filter = 0;
    this.state.index = prop.index;
    let _this = this;
    let unsubscribe = getStateData(function (data) {
      if (data.filter !== undefined) {
        _this.filter(data.filter);
      }
    });


    // emitter.on('filter', this.filter);
  }

  filter(msg) {
    this.setState({
      filter: msg
    });
  }

  //  DOM 
  componentWillUnmount() {
    console.log('unmonent');
    // emitter.removeListener('filter', this.filter);
  }

  render() {
    let el;
    let _this = this;
    if (this.state.filter === 0 || this.state.filter === this.state.state) {
      el = <li className="box swiper-slide">
        <div>
          <Faces url={_this.state.mainurl}/>
          <div className="face-info">
            <p><span></span><i className="fa fa-calendar-check-o" aria-hidden="true"/><span>{this.state.enter}</span>
            </p>
            <p><span></span><i className="fa fa-calendar-times-o" aria-hidden="true"/><span>{this.state.leave}</span>
            </p>
            <p><span></span><span>{this.state.state}</span></p>
          </div>
        </div>
        <With withs={this.state.withs}/>
      </li>;
    } else {
      el = '';
    }

    return el;

  }
}

// 
class With extends React.Component {
  constructor(prop) {
    super(prop);
    this.state = {
      withs: prop.withs
    }

  }

  render() {
    if (!!this.state.withs) {
      return <div className="with">
        <span><br/></span>
        <Witha list={this.state.withs}/>
      </div>
    } else {
      return '';
    }

  }
}

// 
class Witha extends React.Component {
  constructor(prop) {
    super(prop);
    this.numbers = prop.list;
  }


  render() {
    let listItems = this.numbers.map((v, i) =>
      <Withimg key={i} datas={[v, i]}/>
    );
    return <div>{listItems}</div>;
  }
}

class Withimg extends React.Component {
  constructor(prop) {
    super(prop);
    this.state = {
      v: prop.datas[0],
      i: prop.datas[1],
      laym: ''
    };
    this.witha = React.createRef();
  }

  componentDidMount() {
    lazyImg([this.witha.current]);
  }

  openPop(v, id) {
    let _this = this;
    let laym = layer.open({
      className: 'with-content',
      success: function (layer, index) {
        ReactDOM.render(
          <Pop uid={'a' + id} url={v.url}/>,
          document.querySelector('.with-content')
        );
      },
      content: ''
    });

    _this.setState({
      laym: laym
    });

    store.dispatch({type: 'laym', laym: laym});
    // emitter.emit('layerpop', laym);
  }

  render() {
    return <a ref={this.witha}
              data-id={'a' + (this.state.i + 1)}
              data-src={this.state.v.url}
              onClick={this.openPop.bind(this, this.state.v, this.state.i)}/>
  }
}

