class Pop extends React.Component {
  constructor(prop) {
    super(prop);
    this.state = {
      uid: prop.uid,
      url: prop.url,
      laym: prop.laym1,
      style: {},
      loading: 'loading',
      reasonhide: 'none',
      contentBtnshide: '',
      textarea: '',
    };

    this.textareaChange = this.textareaChange.bind(this);
    this.mainCustomer = this.mainCustomer.bind(this);
    this.invaildCustomer = this.invaildCustomer.bind(this);
    this.returnColleague = this.returnColleague.bind(this);
    this.backAccompany = this.backAccompany.bind(this);
    this.submit = this.submit.bind(this);
    this.cancel = this.cancel.bind(this);

    let _this = this;
    let img = new Image;
    img.src = _this.state.url;
    img.onload = function () {
      _this.setState({
        loading: '',
        style: {
          backgroundImage: `url(${_this.state.url})`
        }
      })
    };

    let unsubscribe = getStateData(function (data) {
      // console.log(data)
      // console.log(data, 'laym');
      _this.setState({
        laym: data.laym
      });
    });


    // this.eventEmitter = emitter.addListener('layerpop', (laym) => {
    //   this.setState({
    //     laym: laym
    //   });
    // });
  }

  componentWillUnmount() {
    // emitter.removeListener(this.eventEmitter);
  }

  textareaChange(event) {
    this.setState({textarea: event.target.value});
  }

  mainCustomer() {
    layer.close(this.state.laym);
  }

  invaildCustomer() {
    this.setState({
      reasonhide: '',
      contentBtnshide: 'none'
    });
  }

  returnColleague() {
  }

  backAccompany() {
  }

  submit() {
    this.setState({
      reasonhide: 'none',
      contentBtnshide: ''
    });
    console.log(this.state.textarea);
  }

  cancel() {
    this.setState({
      reasonhide: 'none',
      contentBtnshide: ''
    });
  }

  render() {
    return <div data-id={this.state.uid}>
      <div className={"content-img " + this.state.loading} data-src={this.state.url} style={this.state.style}/>
      <div className={"content-btns " + this.state.contentBtnshide}>
        <button className="main-customer btn btn-primary" onClick={this.mainCustomer.bind(this)}>
          <i className="fa fa-user" aria-hidden="true"/><span></span></button>
        <button className="invaild-customer btn btn-danger" onClick={this.invaildCustomer.bind(this)}>
          <i className="fa fa-warning" aria-hidden="true"/><span></span></button>
        <button className="return-colleague btn btn-success" onClick={this.returnColleague.bind(this)}>
          <i className="fa fa-retweet" aria-hidden="true"/><span></span></button>
        <button className="back-Accompany btn btn-info" onClick={this.backAccompany.bind(this)}>
          <i className="fa fa-users" aria-hidden="true"/><span></span></button>
      </div>
      <div className={"reason " + this.state.reasonhide}>
        <label htmlFor="">
          <span id="reasonTitle"></span>
          <textarea name="" id="" value={this.state.textarea} onChange={this.textareaChange}/>
        </label>
        <div className="reason-btns">
          <button className="submit btn btn-success" onClick={this.submit}></button>
          <button className="cancel btn btn-danger" onClick={this.cancel}></button>
        </div>
      </div>
    </div>
  }
}

export {Facelist, Btnbox};
